import p5 from "p5";
import "p5/lib/addons/p5.sound";

class Star {
    constructor(_x, _y, _r1, _r2, _npoints, _colr, _colg, _colb, sketch) {
        this.x = _x;
        this.y = _y;
        this.r1 = _r1;
        this.r2 = _r2;
        this.npoints = _npoints;
        this.colr = _colr;
        this.colg = _colg;
        this.colb = _colb;

        this.sx = 0;
        this.sy = 0;
        this.angle = 360 / _npoints;
        this.halfAngle = 360 / _npoints / 2;

    }
    createStar(sketch) {
        sketch.noStroke();
        sketch.fill(this.colr, this.colg, this.colb);
        sketch.beginShape();

        for (let j = 0; j < 360; j += this.angle) {
            this.sx = this.x + sketch.cos(j) * this.r2;
            this.sy = this.y + sketch.sin(j) * this.r2;
            sketch.vertex(this.sx, this.sy);
            this.sx = this.x + sketch.cos(j + this.halfAngle) * this.r1;
            this.sy = this.y + sketch.sin(j + this.halfAngle) * this.r1;
            sketch.vertex(this.sx, this.sy);
        }
        sketch.endShape(sketch.CLOSE);
    }

    moveStar(sketch, stars) {
        this.y += sketch.pow(this.r1, 0.9);

        if (this.y > sketch.height) {
            let index = stars.indexOf(this);
            stars.splice(index, 1);
        }
    }
}

const p5Sketch = (sketch, imageUrl) => {
    let mySound, fft, spectrum, height, width, img, canvas;
    let pow2 = 1024;
    let ampCutOut = 120
    let radius = 80;
    let loaded = false;
    let loading = false;
    let closed = false;

    let progress = 0;
    let stars = []

    sketch.playSong = songUrl => {
        if (mySound && mySound.url === songUrl) {
            if (loaded)
                sketch.canvasPressed()
            return false
        }

        loaded = false;
        loading = true;


        if (mySound)
            mySound.pause()

        progress = 0
        mySound = null;
        mySound = sketch.loadSound(songUrl,
            () => {
                loaded = true;
                loading = false;
                if (!closed)
                    mySound.play();
            },
            f => console.log("erÂ§or: " + f),
            p => {
                progress = p
            }
        );
    }

    sketch.preload = () => img = sketch.loadImage(imageUrl);

    sketch.stop = () => {
        mySound && mySound.pause();
        closed = true
    };

    sketch.canvasPressed = () => {
        if (!mySound)
            return null
        if (mySound.isPlaying()) mySound.pause();
        else mySound.play();
    }

    sketch.windowResized = () => {
        if (sketch.windowWidth < 500) {
            sketch.resizeCanvas(sketch.windowWidth * .7, sketch.windowWidth * .7);
            radius = 50
        }
        height = sketch.height;
        width = sketch.width;
    }

    sketch.setup = () => {
        canvas = sketch.createCanvas(350, 350);
        sketch.windowResized()
        canvas.mousePressed(sketch.canvasPressed);

        //set the parent to div #canvas
        canvas.parent('canvas')

        //set angle to degrees, for an easier circle
        sketch.angleMode(sketch.DEGREES);

        fft = new p5.FFT(0.7, pow2);
    }

    sketch.draw = () => {
        sketch.image(img, 0, 0, width, height);


        if (loaded) {
            //dark overlay to see the effect better
            sketch.background('rgba(0,0,0, 0.25)');

            //get frecuencies
            spectrum = fft.analyze();

            //this is so we start drawing from the center
            sketch.translate(width / 2, height / 2);

            for (let i = 1; i < 360 + 1; i += 4) {
                //Offset for the circle
                let r = spectrum[i] * .2
                r = r * ((Math.sqrt(-r + ampCutOut)) / Math.sqrt(ampCutOut - 10)) | 10

                //Starting point for the circle
                let startX = sketch.cos(i) * radius
                let startY = sketch.sin(i) * radius

                //Ending point for the "circle"
                let x = sketch.cos(i) * (r + radius)
                let y = sketch.sin(i) * (r + radius)

                //draw white line
                sketch.stroke(255);
                sketch.line(startX, startY, x, y)
            }
        } else if (loading) {
            //star animation

            let s = new Star(
                sketch.random(0, width), sketch.random(0, height / 2), sketch.random(2, 4), sketch.random(2, 7),
                5,
                17, 85, 79,
                sketch
            );
            for (let i = 0; i < sketch.random(3); i += 1) {
                stars.push(s);
            }
            for (let star of stars) {
                star.moveStar(sketch, stars);
                star.createStar(sketch);
            }

            sketch.stroke(255);
            sketch.fill(255)
            sketch.rect(0, 4 * height / 5, progress * width, 30)
        }
    }

}

export default p5Sketch;